}
.btn-primary:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  z-index: -1;
  transition: all 0.4s;
}
.btn-primary:hover:after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
